import { CrashManagerType } from "../..";

export const stopAutoMode = function (this: CrashManagerType) {
  this.setAutoStarted(false);
  const { general } = this.fields;
  if (general.playableStatus !== "play") {
    general.playableStatus = "watch";
  }

  const { limitsActualValues } = this.fields.auto.data;
  limitsActualValues.profit = 0;
  limitsActualValues.numberOfBets = 0;
  limitsActualValues.betAmount = 0;

  this.updateAutoLimitsField();
  this.updateManualFields();
};
