import { EventEmitter } from "events";

import notifybarManager from "components/NotifyBar/notifybar-manager";
import { toFixed } from "utils";
import { coinsManager } from "hooks";
import { GetLocaleShape } from "providers";

import {
  ButtonValues,
  CrashAutoManagerEvents,
  CrashConnectionEvents,
  CrashGeneralManagerEvents,
  LiveValues,
} from ".";
import {
  CRASH_BET_AMOUNT_LOCAL_NAME_VALUE,
  CRASH_PAYOUT_LOCAL_NAME_VALUE,
  DEFAULT_CRASH_PAYOUT_VALUE,
} from "./consts";

import {
  calculateChance,
  closeCrashSocket,
  buttonOnClick,
  initCrashSocket,
  reactToAutoLimits,
  reactToAutoParams,
  setGameMode,
  socketListener,
  updateDependentManualFields,
  setManualField,
  setAutoLimits,
  setAutoParams,
  updateManualFields,
  updateAutoParamsField,
  updateAutoLimitsField,
  setIsAutoModeOpen,
  setSelectedCoin,
  setActiveUserId,
  acceptBet,
  manualValuesListener,
  betButtonStatusListener,
  autoLimitsListener,
  autoParamsListener,
  autoIsOpenListener,
  exitTheBet,
  stopAutoMode,
  makeRequest,
} from "./methods";
import { setButtonStatus } from "./methods/controller/updateButtonStatus";
import { Fields } from "./types";

export class CrashGameManager extends EventEmitter {
  fields: Fields;
  intl: GetLocaleShape;
  liveValues: LiveValues = {
    profit: 0,
    amount: 0,
    coin: coinsManager.selectedCoin?.coin || "",
  };
  connection: "online" | "offline" = "online";
  constructor(fields: Fields, intl: GetLocaleShape) {
    super();
    this.fields = fields;
    this.intl = intl;

    const { manual } = this.fields;
    manual.betAmount = toFixed(
      localStorage.getItem(CRASH_BET_AMOUNT_LOCAL_NAME_VALUE) || 0,
    );

    manual.payout =
      localStorage.getItem(CRASH_PAYOUT_LOCAL_NAME_VALUE) ||
      DEFAULT_CRASH_PAYOUT_VALUE;
    manual.chance = calculateChance(manual.payout);
  }

  initCrashSocket = initCrashSocket.bind(this);
  closeCrashSocket = closeCrashSocket;

  setConnection(isOnline: boolean) {
    this.connection = isOnline ? "online" : "offline";
    this.emit(CrashConnectionEvents.CHANGE_CONNECTION, isOnline);
  }
  setGameMode = setGameMode.bind(this);
  setAutoLimits = setAutoLimits.bind(this);
  setAutoParams = setAutoParams.bind(this);
  setManualField = setManualField.bind(this);
  setSelectedCoin = setSelectedCoin.bind(this);
  setActiveUserId = setActiveUserId.bind(this);
  setButtonStatus = setButtonStatus.bind(this);
  setIsAutoModeOpen = setIsAutoModeOpen.bind(this);
  setAutoStarted(value: boolean) {
    this.fields.auto.data.isStarted = value;
    this.emit(
      CrashAutoManagerEvents.CHANGE_AUTO_STARTED_STATUS,
      this.fields.auto.data.isStarted,
    );
  }
  setDisabled() {
    const isDisabled =
      this.fields.general.buttonStatus === ButtonValues.Wait_loading ||
      (this.fields.general.buttonStatus === ButtonValues.Auto_setStarted &&
        this.fields.general.playableStatus === "play");

    this.emit(CrashGeneralManagerEvents.CHANGE_IS_DISABLED_STATUS, isDisabled);
  }

  updateManualFields = updateManualFields.bind(this);
  updateAutoParamsField = updateAutoParamsField.bind(this);
  updateAutoLimitsField = updateAutoLimitsField.bind(this);
  updateDependentManualFields = updateDependentManualFields.bind(this);

  reactToAutoParams = reactToAutoParams.bind(this);
  reactToAutoLimits = reactToAutoLimits.bind(this);

  betButtonStatusListener = betButtonStatusListener.bind(this);
  manualValuesListener = manualValuesListener.bind(this);
  autoIsOpenListener = autoIsOpenListener.bind(this);
  autoParamsListener = autoParamsListener.bind(this);
  autoLimitListener = autoLimitsListener.bind(this);

  connectionListener(callback: (isOnline: boolean) => void) {
    this.addListener(CrashConnectionEvents.CHANGE_CONNECTION, callback);

    return () => {
      this.removeListener(CrashConnectionEvents.CHANGE_CONNECTION, callback);
    };
  }

  autoStartedListener(callBack: (value: boolean) => void) {
    this.addListener(
      CrashAutoManagerEvents.CHANGE_AUTO_STARTED_STATUS,
      callBack,
    );

    return () => {
      this.removeListener(
        CrashAutoManagerEvents.CHANGE_AUTO_STARTED_STATUS,
        callBack,
      );
    };
  }
  disableListener(callBack: (isDisabled: boolean) => void) {
    this.addListener(
      CrashGeneralManagerEvents.CHANGE_IS_DISABLED_STATUS,
      callBack,
    );

    return () => {
      this.removeListener(
        CrashGeneralManagerEvents.CHANGE_IS_DISABLED_STATUS,
        callBack,
      );
    };
  }

  acceptBet = acceptBet.bind(this);
  exitTheBet = exitTheBet.bind(this);
  stopAutoMode = stopAutoMode.bind(this);
  buttonOnClick = buttonOnClick.bind(this);
  socketListener = socketListener.bind(this);

  makeRequest(
    amount: number | string,
    coinName: string,
    cashout: number | string,
    profit: number | string,

    isSaveToLocal = false,
  ) {
    this.liveValues.amount = amount;
    this.liveValues.profit = profit;
    this.liveValues.coin = coinName;
    makeRequest(amount, coinName, cashout, isSaveToLocal);
  }

  updateBalance(balance: string | number) {
    const { general } = this.fields;
    coinsManager.coinValueUpdate(`${balance}`, this.liveValues.coin);
    general.coinSelected.balance = Number(coinsManager.selectedCoin?.balance);
  }

  cashedout(balance: string | number, error?: string) {
    this.updateBalance(balance);

    if (error) {
      this.error(error);
    }
  }

  finalized(coin: string, profit: string) {
    const intlMessage = this.intl.formatMessage({
      id: "crashInterface.messages.win",
    });
    notifybarManager.success({
      message: `${intlMessage} ${profit}${coin}`,
    });

    this.fields.general.playableStatus = "watch";
  }

  error(message?: string) {
    const error = message || "unknown error";
    notifybarManager.error({
      message: error,
      timeOut: 4000,
    });
    console.log("crash auto stop error: ", error);

    this.stopAutoMode();
    this.fields.general.playableStatus = "watch";

    this.setButtonStatus();
  }
}
