import { CrashManagerType, ManualFieldsNames } from "../..";

export const setManualField = function (
  this: CrashManagerType,
  field: ManualFieldsNames,
  value: number | string,
) {
  if (!Number(value) && Number(value) !== 0) {
    return;
  }

  this.fields.manual[field] = value;
  this.updateDependentManualFields(field);
  this.updateManualFields();
};
