import { CrashAutoManagerEvents, CrashManagerType } from "../..";

export const autoIsOpenListener = function (
  this: CrashManagerType,
  callBack: (value: boolean) => void,
) {
  this.addListener(CrashAutoManagerEvents.CHANGE_AUTO_OPEN_STATUS, callBack);

  return () => {
    this.removeListener(
      CrashAutoManagerEvents.CHANGE_AUTO_OPEN_STATUS,
      callBack,
    );
  };
};
