import { crashSocket, track } from "utils";

export const makeRequest = (
  amount: number | string,
  coinName: string,
  cashout: number | string,

  isSaveToLocal = false,
) => {
  const betAmountValue = Number(amount).toFixed(8);

  crashSocket.send({
    type: "place_bet",
    bet_amt: betAmountValue,
    coin: coinName,
    auto_cashout: cashout,
  });

  if (isSaveToLocal) {
    try {
      localStorage.setItem(
        "crashBet",
        `{"amt":"${betAmountValue}","coin":"${coinName}"}`,
      );
    } catch (e) {
      track()?.errors(e);
    }
  }
};
