import { crashSocket } from "utils";
import { CrashManagerType } from "../..";

export const exitTheBet = function (this: CrashManagerType, manual = false) {
  const { fields } = this;
  const { data } = fields.auto;

  if (data.isStarted) {
    data.winStatus = "win";
  }

  if (manual) {
    this.fields.general.isWaitServerAnswer = true;
    crashSocket.send({ type: "cashout_bet" });
  } else {
    fields.general.playableStatus = "watch";
    this.setButtonStatus();
  }
  localStorage.removeItem("crashBet");
};
