import notifybarManager from "components/NotifyBar/notifybar-manager";
import { CrashManagerType } from "../..";

export const acceptBet = function (
  this: CrashManagerType,
  balance: string | number,
  error?: string,
) {
  this.updateBalance(balance);
  const { general } = this.fields;
  general.playableStatus = "play";

  if (!error) {
    notifybarManager.success({
      message: "Bet successfully placed!",
    });
  } else {
    this.error(error);
  }
};
