import { CrashAutoManagerEvents, CrashManagerType } from "../..";

export const setIsAutoModeOpen = function (
  this: CrashManagerType,
  value: boolean,
) {
  const { auto, general } = this.fields;
  const { data } = auto;
  data.isOpen = value;
  this.setAutoStarted(false)

  if (general.playableStatus !== "play") {
    general.playableStatus = "watch";
  }

  this.setButtonStatus();
  this.emit(CrashAutoManagerEvents.CHANGE_AUTO_OPEN_STATUS, data.isOpen);
};
