import {
  CrashManagerType,
  CrashManualManagerEvents,
  ManualFieldsValues,
} from "../..";

export const manualValuesListener = function (
  this: CrashManagerType,
  callBack: (value: ManualFieldsValues) => void,
) {
  this.addListener(
    CrashManualManagerEvents.CHANGE_MANUAL_VALUES_EVENT,
    callBack,
  );

  return () => {
    this.removeListener(
      CrashManualManagerEvents.CHANGE_MANUAL_VALUES_EVENT,
      callBack,
    );
  };
};
