import { crashSocketWatcher } from "pages/Crash/crashSocketWatcher";
import { crashSocket, track } from "utils";
import { CrashManagerType } from "../..";

export const initCrashSocket = function (this: CrashManagerType) {
  const sendInitialData = async () => {
    let selectedCoinName = "GEM";

    try {
      const savedCoin = localStorage.getItem("selectedCoin");
      if (savedCoin) {
        const parsedCoin = JSON.parse(savedCoin);
        if (parsedCoin.coin) {
          selectedCoinName = parsedCoin.coin;
        }
      }
    } catch (e) {
      track()?.errors(e);
      console.error("error when trying to get selected coin");
      selectedCoinName = "";
    }

    const data = await fetch(
      "https://api.pasino.com/account/get-socket-token",
      {
        body: JSON.stringify({
          token: localStorage.getItem("token"),
        }),
        method: "POST",
      },
    )
      .then((r) => r.json())
      .then((r) => r)
      .catch((e) => {
        console.error("Crash get-socket-token error: ", e);
        track()?.errors(e);

        return null;
      });

    const sendData = [
      {
        type: "initialization",
        socket_token: data?.socket_token || "",
      },
      {
        type: "get_current_bets",
      },

      { type: "get_previous_rounds" },
      {
        type: "get_coin_parameters",
        coin: selectedCoinName,
      },
    ];

    sendData.forEach((param) => {
      crashSocket.send(param);
    });
  };

  crashSocket.open(sendInitialData);
  crashSocket.listener();
  crashSocket.dispatch(crashSocketWatcher);

  this.socketListener();
};
