import { rangeBetween } from "utils";
import { CrashManagerType } from "../..";

export const reactToAutoParams = function (this: CrashManagerType) {
  const { auto, manual, general } = this.fields;
  const { data, values } = auto;
  const { coinData } = general;

  const autoParamKey =
    data.winStatus === "loss"
      ? "onLoss"
      : data.winStatus === "win"
      ? "onWin"
      : null;

  if (autoParamKey) {
    const { value, isChangePayout, payoutValue, betPercentValue } =
      values.params[autoParamKey];

    switch (value) {
      case 0:
        let newBetAmount =
          Number(manual.betAmount) +
          Number(manual.betAmount) * (Number(betPercentValue) / 100);
        if (coinData) {
          newBetAmount = rangeBetween(
            newBetAmount,
            coinData.minimum_bet,
            coinData.maximum_bet,
          );
        }

        manual.betAmount = newBetAmount.toFixed(8);
        break;

      case 1:
        manual.betAmount = data.baseBet;
        break;

      case 2:
        this.stopAutoMode();
        break;

      default:
        break;
    }

    if (isChangePayout) {
      manual.payout = Number(payoutValue);
    }
  }

  this.updateManualFields();
};
