import {
  CrashAutoManagerEvents,
  CrashAutoParams,
  CrashManagerType,
} from "../..";

export const autoParamsListener = function (
  this: CrashManagerType,
  callBack: (data: CrashAutoParams) => void,
) {
  this.addListener(CrashAutoManagerEvents.CHANGE_AUTO_PARAMS_EVENT, callBack);

  return () => {
    this.removeListener(
      CrashAutoManagerEvents.CHANGE_AUTO_PARAMS_EVENT,
      callBack,
    );
  };
};
