import {
  ButtonValues,
  CrashGeneralManagerEvents,
  CrashManagerType,
} from "../..";

export const betButtonStatusListener = function (
  this: CrashManagerType,
  callback: (buttonValue: ButtonValues) => void,
) {
  this.addListener(
    CrashGeneralManagerEvents.CHANGE_BUTTON_STATUS_EVENT,
    callback,
  );

  return () => {
    this.removeListener(
      CrashGeneralManagerEvents.CHANGE_BUTTON_STATUS_EVENT,
      callback,
    );
  };
};
