import React, { FC, useEffect, useMemo } from "react";
import { useGetLocale } from "providers";
import { CrashGameManager, fields } from "..";

import Context from "./Context";

export const CrashControlProvider: FC = ({ children }) => {
  const intl = useGetLocale();

  const manager = useMemo(() => new CrashGameManager(fields, intl), []);

  useEffect(() => {
    return () => {
      manager?.removeAllListeners();
    };
  }, []);

  return <Context.Provider value={{ manager }}>{children}</Context.Provider>;
};

export default CrashControlProvider;
