import { calculateChance, calculateProfit } from "..";
import {
  CRASH_BET_AMOUNT_LOCAL_NAME_VALUE,
  CRASH_PAYOUT_LOCAL_NAME_VALUE,
} from "../../consts";
import { CrashManagerType, ManualFieldsNames } from "../../types";

export const updateDependentManualFields = function (
  this: CrashManagerType,
  field?: ManualFieldsNames,
) {
  const { manual } = this.fields;
  const setProfit = () => {
    manual.profit = calculateProfit(manual.betAmount, manual.payout);
  };
  const setChance = () => {
    manual.chance = calculateChance(manual.payout);
  };

  const saveBetAmount = () => {
    localStorage.setItem(
      CRASH_BET_AMOUNT_LOCAL_NAME_VALUE,
      `${manual.betAmount}`,
    );
  };
  const savePayout = () => {
    localStorage.setItem(CRASH_PAYOUT_LOCAL_NAME_VALUE, `${manual.payout}`);
  };

  if (field) {
    switch (field) {
      case "betAmount":
        setProfit();
        saveBetAmount();
        break;
      case "payout":
        setProfit();
        setChance();
        savePayout();
        break;

      default:
        break;
    }
  } else {
    setProfit();
    setChance();
  }
};
