import { toFixed } from "utils";
import { checkIsSupport } from "./checkIsSupport";

export const calculateProfit = (
  betAmount: number | string,
  payout: number | string,
) => {
  let profit = Number(
    toFixed(Number(betAmount) * Number(payout) - Number(betAmount)),
  );

  profit = checkIsSupport(profit);

  return profit;
};
