import {
  ButtonValues,
  CrashGeneralManagerEvents,
  CrashManagerType,
} from "../../types";

export const setButtonStatus = function (this: CrashManagerType) {
  const { general, auto } = this.fields;
  const setStatus = (status: ButtonValues) => {
    general.buttonStatus = status;
  };

  if (!auto.data.isOpen) {
    //manual
    switch (general.gameMode) {
      case "loading":
        setStatus(ButtonValues.Bet_nextRound);
        break;
      case "new_round":
        switch (general.playableStatus) {
          case "watch":
            setStatus(ButtonValues.Bet);
            break;

          case "play":
          default:
            setStatus(ButtonValues.Wait_loading);
            break;
        }
        break;
      case "update_value":
        switch (general.playableStatus) {
          case "play":
            setStatus(ButtonValues.Cashout);
            break;

          case "watch":
            setStatus(ButtonValues.Bet_nextRound);
            break;

          case "wait":
          default:
            setStatus(ButtonValues.Wait_cancelBet);
            break;
        }
        break;
      case "crashed":
        switch (general.playableStatus) {
          case "wait":
            setStatus(ButtonValues.Wait_cancelBet);
            break;

          case "watch":
          default:
            setStatus(ButtonValues.Bet_nextRound);
            break;
        }
        break;

      default:
        break;
    }
  } else {
    //auto

    if (auto.data.isStarted) {
      setStatus(ButtonValues.Auto_setNoStarted);
    } else {
      setStatus(ButtonValues.Auto_setStarted);
    }
  }

  this.emit(
    CrashGeneralManagerEvents.CHANGE_BUTTON_STATUS_EVENT,
    this.fields.general.buttonStatus,
  );
  this.setDisabled();
};
