import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";
import { crashSocketsList } from "pages/Crash/crashSocketWatcher";
import { ManagerListenerProps } from "pages/Crash/crashTypes";
import { crashSocket, rangeBetween, toFixed } from "utils";
import { CrashManagerType } from "../../types";

export function socketListener(this: CrashManagerType) {
  const { general, manual } = this.fields;
  const managerListener = (data: ManagerListenerProps) => {
    switch (data.method) {
      case "update_coin_parameters":
        const min = Number(data.coinPrice.minimum_bet),
          max = Number(data.coinPrice.maximum_bet);

        general.coinData = {
          maximum_bet: max,
          minimum_bet: min,
          maximum_profit: Number(data.coinPrice.maximum_profit),
          price: Number(data.coinPrice?.price),
        };
        manual.betAmount = toFixed(
          rangeBetween(Number(manual.betAmount), min, max),
        );
        this.updateDependentManualFields();
        this.updateManualFields();

        break;

      case "crashed":
      case "new_round":
      case "update_value":
        this.setGameMode(data.method);
        break;

      case "bet_played":
        debugger;
        this.acceptBet(data.balance, data.error);
        break;

      case "bet_cashedout":
        this.cashedout(data.balance, data.error);
        break;

      case "bet_cashedout_update":
        if (data.user_id === general.userId) {
          this.exitTheBet();
        }
        break;

      case "new_bet_finalized":
        this.finalized(data.coin, data.profit);
        this.fields.general.isWaitServerAnswer = false;

        liveStatsManager.addBet({
          amount: Number(data.amount),
          profit: Number(data.profit),
          coin: data.coin,
        });
        break;

      case "offline":
        this.stopAutoMode();
        this.fields.general.playableStatus = "watch";
        this.setButtonStatus();

        const payoutText = document.getElementById("crash-payout-text");
        if (payoutText) payoutText.innerText = "...";

        this.setConnection(false);
        break;

      case "online":
        if (this.connection === "offline") {
          crashSocket.send({ type: "get_current_bets" });
        }
        this.setConnection(true);
        break;

      case "current_bets":
        const { userId, playableStatus } = this.fields.general;
        if (
          data?.bets[userId] &&
          !data.bets[userId].profit &&
          playableStatus !== "play"
        ) {
          this.fields.general.playableStatus = "play";
          this.setButtonStatus();
        }
        break;

      default:
        break;
    }
  };
  crashSocketsList.managerListener = managerListener;
}
