import {
  CrashAutoLimits,
  CrashAutoManagerEvents,
  CrashManagerType,
} from "../..";

export const autoLimitsListener = function (
  this: CrashManagerType,
  callBack: (data: CrashAutoLimits) => void,
) {
  this.addListener(CrashAutoManagerEvents.CHANGE_AUTO_LIMITS_EVENT, callBack);

  return () => {
    this.removeListener(
      CrashAutoManagerEvents.CHANGE_AUTO_LIMITS_EVENT,
      callBack,
    );
  };
};
