import { notifyBarManager } from "components";
import { CrashManagerType } from "../..";

export const buttonOnClick = function (this: CrashManagerType) {
  const token = localStorage.getItem("token");
  if (!token) {
    const message = this.intl.formatMessage({ id: "plinko.bet.notLoggedIn" });
    notifyBarManager.error({
      message,
    });
    return;
  }
  const { general, manual, auto } = this.fields;
  const { betAmount, payout, profit } = manual;
  const { name } = general.coinSelected;
  const { data } = auto;
  if (!data.isOpen) {
    switch (
      general.gameMode //manual
    ) {
      case "update_value":
      case "crashed":
        switch (general.playableStatus) {
          case "watch":
            general.playableStatus = "wait";
            break;
          // @ts-ignore -- fallthrough
          case "play":
            this.exitTheBet(true);
          /*fallthrough*/
          case "wait":
          default:
            general.playableStatus = "watch";
            break;
        }
        break;
      case "loading":
        general.playableStatus = "watch";
        break;

      case "new_round":
        general.playableStatus = "wait";
        this.makeRequest(betAmount, name, payout, profit, true);

        break;

      default:
        break;
    }
  } else {
    //auto
    const setAutoStarted = () => {
      data.winStatus = "noPlay";
      this.setAutoStarted(!data.isStarted);

      if (general.playableStatus !== "play") {
        general.playableStatus = data.isStarted ? "wait" : "watch";
      }

      if (data.isStarted) {
        data.baseBet = manual.betAmount;
      } else {
        this.stopAutoMode();
      }
    };

    switch (general.gameMode) {
      case "new_round":
        setAutoStarted();
        if (data.isStarted) {
          this.makeRequest(betAmount, name, payout, profit);
        }

        break;
      // @ts-ignore -- fallthrough
      case "update_value":
        if (general.playableStatus === "play") {
          this.exitTheBet(true);
        }
      /*fallthrough*/
      case "crashed":
        setAutoStarted();

        break;

      case "loading":
      default:
        general.playableStatus = "watch";
        this.setAutoStarted(false);
        break;
    }
  }

  this.setButtonStatus();
};
