import { CrashManagerType } from "../..";

export const reactToAutoLimits = function (this: CrashManagerType) {
  const { auto, manual } = this.fields;
  const { data, values } = auto;
  const { limitsActualValues } = data;

  limitsActualValues.numberOfBets += 1;
  limitsActualValues.betAmount += Number(manual.betAmount);

  switch (data.winStatus) {
    case "loss":
      limitsActualValues.profit -= Number(manual.betAmount);
      break;
    case "win":
      limitsActualValues.profit +=
        Number(manual.betAmount) * Number(manual.payout) -
        Number(manual.betAmount);
      break;
    default:
      break;
  }

  const {
    profit,
    numberOfBets: betsValue,
    betAmount: generalBetAmount,
  } = limitsActualValues;
  const { stopOnLoss, stopOnProfit, numberOfBets, maxBetAmount } =
    values.limits;

  if (Number(stopOnLoss) && Number(stopOnLoss) * -1 - profit >= 0) {
    this.stopAutoMode();
  }

  if (Number(stopOnProfit) && Number(stopOnProfit) - profit <= 0) {
    this.stopAutoMode();
  }

  if (Number(numberOfBets) && Number(numberOfBets) - betsValue <= 0) {
    this.stopAutoMode();
  }

  if (Number(maxBetAmount) && Number(maxBetAmount) - generalBetAmount <= 0) {
    this.stopAutoMode();
  }

  this.updateAutoLimitsField();
};
