import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";
import { CrashGameCircleValue, CrashManagerType } from "../..";

export const setGameMode = function (
  this: CrashManagerType,
  mode: CrashGameCircleValue,
) {
  const { general, manual, auto } = this.fields;
  const { betAmount, payout, profit } = manual;
  const { name } = general.coinSelected;
  const { data } = auto;
  general.gameMode = mode;
  const crashPayoutText = document.getElementById(
    "crash-payout-text",
  ) as HTMLTitleElement | null;

  const payoutClassList = crashPayoutText?.classList;
  switch (mode) {
    case "crashed":
      if (
        general.playableStatus === "play" ||
        this.fields.general.isWaitServerAnswer
      ) {
        data.winStatus = "loss";
        const { liveValues } = this;
        liveStatsManager.addBet({
          amount: Number(liveValues.amount),
          profit: Number(-liveValues.amount),
          coin: name,
        });

        payoutClassList?.add("canvasVarelaError");
      }

      if (!data.isStarted) {
        //manual
        localStorage.removeItem("crashBet");

        switch (general.playableStatus) {
          case "wait":
            general.playableStatus = "wait";
            break;
          case "play":
          case "watch":
          default:
            general.playableStatus = "watch";
            break;
        }
      } else {
        //auto

        general.playableStatus = "wait";
        this.reactToAutoLimits();
        this.reactToAutoParams();
      }

      this.fields.general.isWaitServerAnswer = false;
      break;

    case "new_round":
      payoutClassList?.remove("canvasVarelaError");

      switch (general.playableStatus) {
        case "wait":
          this.makeRequest(betAmount, name, payout, profit, !data.isStarted);

          break;

        default:
          break;
      }
      break;

    case "update_value":
      if (data.isStarted && general.playableStatus !== "play") {
        general.playableStatus = "wait";
      } else if (
        data.isOpen &&
        !data.isStarted &&
        general.playableStatus === "play"
      ) {
        setTimeout(() => {
          this.exitTheBet(true);
        }, 111);
      }
      break;

    default:
      break;
  }

  this.setButtonStatus();
};
