import { CrashGameManager } from ".";

export interface LimitsProps {
  numberOfBets: number | string;
  stopOnLoss: number | string;
  stopOnProfit: number | string;
  maxBetAmount: number | string;
}

export type AutoOnRadioValues = 0 | 1 | 2;

export interface AutoOnValues {
  value: AutoOnRadioValues;
  isChangePayout: boolean;
  payoutValue: string;
  betPercentValue: string;
}

export interface OnParamsProps {
  onWin: AutoOnValues;
  onLoss: AutoOnValues;
}

export type CrashManagerType = CrashGameManager;
export type ManualFieldsNames = "betAmount" | "profit" | "payout" | "chance";

export type ManualFieldsValues = {
  [Key in ManualFieldsNames]: number | string;
};

export interface LiveValues {
  profit: number | string;
  amount: number | string;
  coin: string;
}

export interface CrashManagerCoinParams {
  maximum_bet: number;
  maximum_profit: number;
  minimum_bet: number;
  price: number;
}

export interface CrashSelectedCoinData {
  balance: number;
  name: string;
}

export type CrashGameCircleValue = "crashed" | "new_round" | "update_value";
export type PlayerPlayableStatus = "watch" | "wait" | "play";

export interface GeneralDataFields {
  coinData?: CrashManagerCoinParams;
  coinSelected: CrashSelectedCoinData;
  gameMode: CrashGameCircleValue | "loading";
  buttonStatus: ButtonValues;
  playableStatus: PlayerPlayableStatus;
  userId: number | string;

  isWaitServerAnswer: boolean;
}

export type CrashAutoLimits = LimitsProps;
export type CrashAutoParams = OnParamsProps;

export interface AutoFieldsValues {
  data: {
    isOpen: boolean;
    isStarted: boolean;
    winStatus: "win" | "loss" | "noPlay";

    baseBet: number | string;
    limitsActualValues: {
      numberOfBets: number;
      profit: number;
      betAmount: number;
    };
  };
  values: {
    limits: CrashAutoLimits;
    params: CrashAutoParams;
  };
}
export interface Fields {
  manual: ManualFieldsValues;
  auto: AutoFieldsValues;
  general: GeneralDataFields;
}

export enum CrashManualManagerEvents {
  CHANGE_MANUAL_VALUES_EVENT = "CHANGE_MANUAL_VALUES_EVENT",
}

export enum CrashGeneralManagerEvents {
  CHANGE_BUTTON_STATUS_EVENT = "CHANGE_BUTTON_STATUS_EVENT",
  CHANGE_IS_DISABLED_STATUS = "CHANGE_IS_DISABLED_STATUS",
}

export enum CrashAutoManagerEvents {
  CHANGE_AUTO_OPEN_STATUS = "CHANGE_AUTO_OPEN_STATUS",
  CHANGE_AUTO_STARTED_STATUS = "CHANGE_AUTO_STARTED_STATUS",
  CHANGE_AUTO_PARAMS_EVENT = "CHANGE_AUTO_PARAMS_EVENT",
  CHANGE_AUTO_LIMITS_EVENT = "CHANGE_AUTO_LIMITS_EVENT",
}

export type ManualListenerProps = {
  field: ManualFieldsNames;
  callBack: (data: any) => void;
};

export enum ButtonValues {
  Bet,
  Bet_nextRound,
  Wait_cancelBet,
  Wait_loading,
  Cashout,

  Auto_setStarted,
  Auto_setNoStarted,
}

export enum CrashConnectionEvents {
  CHANGE_CONNECTION = "CHANGE_CONNECTION",
}
